import { environment } from "@rp/common/environment"
import { FeaturesFlags } from "@rp/common/environment/types"

export const getEnvironmentValueOrDefault = (key: string, defaultValue: string) => {
  const value = localStorage.getItem(key)
  if (value === null) return defaultValue
  return value
}

export const getLocalStorageFlag = (key: FeaturesFlags) => {
  const value = getEnvironmentValueOrDefault(key, environment.flags[key].toString())
  return value === "true"
}
